/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef, useState } from "react";

// ÂâçÁ´ØÈÅøÂÖçÊîπÂèò‰º†ÂÖ•ÁöÑ‰ΩøÁî®ÂØπË±°
export const isFalse = (value: unknown) => (value === 0 ? false : !value);

// ÈÅøÂÖçÂà†Èô§ÂèØÁî®Â±ûÊÄßÔºåÊØîÂ¶Ç {xxx: false}
export const isVoid = (value: unknown) =>
  value === undefined || value === null || value === "";

export const cleanObject = (object: { [key: string]: unknown }) => {
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  });
  return result;
};

// custom‚Äîhook Ëá™ÂÆö‰πâhooks,ÂàùÂßãÂåñÂè™Âä†ËΩΩ‰∏ÄÊ¨°
export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
  }, []);
};

export const useDebounce = <T>(value: T, delay: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(() => {
    // ÊØèÊ¨°Âú®valueÂèòÂåñ‰ª•ÂêéÔºåËÆæÁΩÆ‰∏Ä‰∏™ÂÆöÊó∂Âô®
    const timeout = setTimeout(() => setDebouncedValue(value), delay);
    // ÊØèÊ¨°Âú®‰∏ä‰∏Ä‰∏™useEffectÂ§ÑÁêÜÂÆå‰ª•ÂêéÂÜçËøêË°å
    return () => clearTimeout(timeout);
  }, [value, delay]);

  return debouncedValue;
};

export const useDocumentTitle = (
  title: string,
  keepOnUnmunt: boolean = true
) => {
  //‰ΩøÁî®useRef‰ª£ÊõøÈó≠ÂåÖÔºåÂÆûÁé∞keepOnUnmunt
  const oldTitle = useRef(document.title).current;

  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(() => {
    return () => {
      if (!keepOnUnmunt) {
        document.title = oldTitle;
      }
    };
  }, [keepOnUnmunt, oldTitle]);
};

export const resetRoute = () => (window.location.href = window.location.origin);

/**
 * ‰º†ÂÖ•‰∏Ä‰∏™ÂØπË±°ÔºåÂíåÈîÆÈõÜÂêàÔºåËøîÂõûÂØπÂ∫îÁöÑÂØπË±°‰∏≠ÁöÑÈîÆÂÄºÂØπ
 * @param obj
 * @param keys
 */
export const subset = <
  O extends { [key in string]: unknown },
  K extends keyof O
>(
  obj: O,
  keys: K[]
) => {
  const filteredEntries = Object.entries(obj).filter(([key]) =>
    keys.includes(key as K)
  );
  return Object.fromEntries(filteredEntries) as Pick<O, K>;
};


/**
 *  ËøîÂõûÁªÑ‰ª∂ÁöÑÊåÇËΩΩÁä∂ÊÄÅÔºå Â¶ÇÊûúËøòÊåÇËΩΩÊàñËÄÖÂ∑≤ÁªèÊåÇËΩΩÔºåËøîÂõûfalse, Âèç‰πãËøîÂõûtrue
 *  
 * */ 

export const useMountedRef=()=>{

  const mountedRef = useRef(false);

  useEffect(() => {
    mountedRef.current = true;

    return ()=>{
      mountedRef.current = false;
    }
  })
  return mountedRef
}