import React from "react";
import { Raw } from "types";
import { Select } from "antd";

// Êö¥Èú≤‰ΩøÁî®ÁªÑ‰ª∂Êó∂Ëá™ÂÆö‰πâÁöÑÂ±ûÊÄß
type SelectProps = React.ComponentProps<typeof Select>;

interface IdSelectProps
  extends Omit<SelectProps, "value" | "onChange" | "options"> {
  value?: Raw | null | undefined;
  onChange?: (value?: number) => void;
  defaultOptionName?: string;
  options?: { name: string; id: number }[];
}

/**
 * value ÂèØ‰ª•‰º†ÂÖ•Â§öÁßçÁ±ªÂûãÁöÑÂÄº
 * onChangeÂè™‰ºöÂõûË∞É number|undefined Á±ªÂûã
 * ÂΩì isNaN(Number(value)) ‰∏∫trueÁöÑÊó∂ÂÄôÔºå‰ª£Ë°®ÈÄâÊã©ÈªòËÆ§Á±ªÂûã
 * ÂΩìÈÄâÊã©ÈªòËÆ§Á±ªÂûãÁöÑÊó∂ÂÄôÔºåonChange‰ºöÂõûË∞Éundefined
 * @param props
 * @constructor
 */
export const IdSelect = (props: IdSelectProps) => {
  const { value, onChange, defaultOptionName, options, ...restProps } = props;
  return (
    <Select
      // ÊîπÂèòantd selectÁöÑÈªòËÆ§Ë°å‰∏∫ 
      value={options?.length ? toNumber(value) : 0}
      onChange={(value) => onChange?.(toNumber(value) || undefined)}
      {...restProps}
    >
      {defaultOptionName ? (
        <Select.Option value={0}>{defaultOptionName}</Select.Option>
      ) : null}
      {options?.map((option) => (
        <Select.Option key={option.id} value={option.id}>
          {option.name}
        </Select.Option>
      ))}
    </Select>
  );
};

const toNumber = (value: unknown) => (isNaN(Number(value)) ? 0 : Number(value));
